import {Component, Injector, OnInit} from '@angular/core';
import DataSource from 'devextreme/data/data_source';
import {BaseComponent} from '../../../@calls/components/base/base.component';

@Component({
    selector: 'app-companies',
    templateUrl: './companies.component.html',
    styleUrls: ['./companies.component.scss']
})
export class CompaniesComponent extends BaseComponent implements OnInit {
    isLoading = false;
    dataSource: any;

    constructor(injector: Injector) {
        super(injector);
    }

    ngOnInit(): void {
        this.getCompanies();
    }

    async deleteItem(item) {
        const result = await this.utilService.confirmationDialog(this.translateService.instant('app.warning'),
            this.translateService.instant('app.removeCaution'));
        result.subscribe((res) => {
            if (res) {
                this.isLoading = true;
                this.companyService.removeCompany(item.id).subscribe((res) => {
                    this.getCompanies();
                    this.utilService.showDeletedSuccessfullyToast();
                    this.isLoading = false;
                }, (err) => {
                    console.log(err);
                    this.isLoading = false;
                    this.utilService.showConnectionErrorToast();
                });
            }
        });
    }


    getCompanies(): void {
        this.companyService.getCompanies().subscribe((res) => {
            this.dataSource = new DataSource(res.content.data);
        });
    }

    updateItem(data): void {
        this.router.navigateByUrl('/pages/add-company', {state: {edit: true, data}}).then();
    }
}
